<template lang="pug">
  div(class="user-panel")
    div(class="user-info")
      div(class="avatar" @click="openModal")
        div(class="portrait")
      div(class="name-group")
        div(class="username")
          p {{user.username}}
          label(class="nickname") {{user.nickname}}
        div(class="lv-info")
          span {{planet.name}}
          span Level. {{planet.level}}
    div(v-if="mode === 'Game'" class="info-group")
      div(class="charge-panel")
        i(:style="{'background-image':`url(${require('@/assets/icon/diamond.png')})`}")
        span {{user.diamond}}
        i
      div(class="charge-panel")
        i(:style="{'background-image':`url(${require('@/assets/icon/coin.png')})`}")
        span {{user.coin}}
        i
      div(class="planet-info" @click="isPlanetInfoOpen = !isPlanetInfoOpen")
        div(class="drop-down-wrapper" :class="{'drop-down--wrap-border': !isPlanetInfoOpen}") 星球資訊
          i(:style="{'transform': isPlanetInfoOpen ? 'rotate(0deg)' : 'rotate(180deg)'}")
        div(v-if="isPlanetInfoOpen" class="drop-down-list")
          div(v-for="(value, key) in planet.indexes")
            i
            span {{key}}
            span {{value}}
    div(v-if="mode === 'Game'" class="button-group")
      i(class="flag" @click="backToOrig")
      router-link(tag="i" to="/home" class="back" @click.native="backToPlanetScene")
</template>

<script>
export default {
  props: {
    mode: String,
    user: Object,
    planet: Object
  },
  data () {
    return {
      module: null,
      isPlanetInfoOpen: false
    }
  },
  beforeCreate () {
    import('@/game/scenes/PlayScene').then(async (module) => {
      this.module = module
    })
  },
  created () {
    const pplGrowth = parseInt(this.planet.indexes['人口成長'])
    this.planet.indexes['人口成長'] = pplGrowth >= 0 ? `+${pplGrowth}/hr` : `-${pplGrowth}/hr`
  },
  methods: {
    openModal () {
      this.$emit('openModal', 'user-profile')
    },
    backToOrig () {
      this.module.gameFunction.backToOrig()
    },
    backToPlanetScene () {
      this.module.gameFunction.backToPlanetScene()
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";
.user-panel {
  .user-info {
    position: fixed;
    top: 27px;
    left: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .avatar {
      position: absolute;
      box-sizing: border-box;
      width: 64px;
      height: 64px;
      background: linear-gradient(
        90deg,
        rgba(73, 103, 219, 0.8),
        rgba(170, 105, 181, 0.8)
      );
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      .portrait {
        width: 56px;
        height: 56px;
        background: white;
        background-image: url("~@/assets/icon/city-chaser-icon.png");
        background-position: center center;
        background-size: contain;
        background-repeat: no-repeat;
        border-radius: 50%;
      }
    }
    .name-group {
      .username {
        padding: 0px 0px 3px 73px;
        display: flex;
        > p:nth-child(1) {
          font-size: 18px;
          margin-right: 6px;
          text-shadow: map-get($shadow, secondary-8);
        }
        > label.nickname {
          align-self: center;
          font-size: 10px;
          padding: 0px 5px;
          display: block;
          border-radius: 4px;
          background-color: #4568dc;
          box-shadow: map-get($shadow, secondary-4);
        }
      }
      .lv-info {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        width: 103px;
        background: linear-gradient(
          90deg,
          rgba(73, 103, 219, 0.8),
          rgba(170, 105, 181, 0.8)
        );
        box-shadow: map-get($shadow, secondary-4);
        border-radius: 6px;
        margin-left: 40px;
        padding: 0px 8px 0 20px;
        > span {
          font-size: 10px;
        }
      }
    }
  }
  .info-group {
    position: fixed;
    top: 39px;
    right: 15px;
    > div:nth-child(1),
    > div:nth-child(2) {
      width: 98px;
      height: 16px;
      margin-bottom: 18px;
      padding-right: 20px;
      > i:nth-child(1) {
        position: absolute;
        left: 0px;
        top: -20px;
        width: 40px;
        height: 38px;
      }
      > i:nth-child(3) {
        position: absolute;
        right: 0;
        width: 16px;
        height: 16px;
      }
    }
    > .planet-info {
      background: rgba(#1b1229, 0.7);
      > .drop-down-wrapper {
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 5px;
        width: 98px;
        height: 18px;
        font-size: 9px;
        line-height: 10px;
        background-color: map-get($colors, primary);
        box-shadow: map-get($shadow, secondary-4);
        border-top-right-radius: 2px;
        border-top-left-radius: 2px;
        > i {
          background-image: url("~@/assets/icon/arrow.png");
          width: 6px;
          height: 6px;
        }
      }
      > .drop-down--wrap-border {
        border-radius: 2px;
      }
      > .drop-down-list {
        box-sizing: border-box;
        border: 2px solid map-get($colors, primary);
        border-top: none;
        border-bottom-right-radius: 2px;
        border-bottom-left-radius: 2px;
        width: 98px;
        padding-top: 4px;
        > div {
          box-sizing: border-box;
          width: 100%;
          padding-left: 13px;
          padding-right: 3px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          position: relative;
          margin-bottom: 4px;
          > span {
            font-size: 9px;
          }
          > i {
            position: absolute;
            left: 4px;
            top: 2px;
            width: 7px;
            height: 9px;
          }
          &:nth-child(1) > i {
            background-image: url("~@/assets/icon/feature.png");
          }
          &:nth-child(2) > i {
            background-image: url("~@/assets/icon/power.png");
          }
          &:nth-child(3) > i {
            background-image: url("~@/assets/icon/population.png");
          }
        }
      }
    }
  }
  .button-group {
    display: flex;
    position: fixed;
    top: 104px;
    left: 39px;
    > i {
      width: 24px;
      height: 24px;
      border-radius: 50%;
      box-shadow: map-get($shadow, secondary-4);
      background-color: #fcfcfc;
    }
    > i:nth-child(1) {
      background-image: url("~@/assets/icon/position-flag.png");
      margin-right: 10px;
    }
    > i:nth-child(2) {
      background-image: url("~@/assets/icon/back.png");
    }
  }
}
</style>
