<template lang="pug">
  div(class="robot-root")
    div
      h1 K-bot
      h5 擁有數： 2
    div
      p {{content}}
      img(:src="require('@/assets/robot-normal.png')")
    div
      div(class="heading-default")
        h4 採集器狀態
    div
      div(class="waiting" :class="{'cool-down': true}")
        img(:src="require('@/assets/robot-cool-down.png')")
        p 冷卻中 01:10
      div(class="waiting" :class="{'cool-down': false}")
        img(:src="require('@/assets/robot-normal.png')")
        p 待命中
    div
      button 直接結束冷卻
    button(class="leave-btn" @click="$emit('closeRobot')")
</template>
<script>
import ModalCardContent from '@/components/ModalCardContent.vue'
export default {
  components: {
    ModalCardContent
  },
  data: function () {
    return {
      robotpage: true,
      content: '一隻貓被鎖在一個箱子中 ， 箱中的貓同時既是活 ， 也是死 。 當箱子打開時 ， 此量子疊加狀態瓦解 ； 在那瞬間這隻貓是被毒死 ， 或得以保命。'
    }
  },
  created () {
    console.log('i am robot')
  }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";
.robot-root{
  display: grid;
  grid-template-rows: 24px 12px 265px 15px 23px 17px 100px 26px;
  grid-template-areas: "robot-title"
    "."
    "robot-content"
    "."
    "state-title"
    "."
    "state-content"
    "button";
  > div:nth-child(1){
    grid-area: robot-title;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    position: relative;
    > h1 {
      font-size: 24px;
      text-shadow: map-get($shadow, secondary-4);
    }
    > h5 {
      font-size: 10px;
      line-height: 20px;
      position: absolute;
      right: 0;
    }
  }
  > div:nth-child(2){
    grid-area: robot-content;
    border-top: solid 1px map-get($colors,primary);
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-rows: 17px 42px 17px 178px 16px;
    grid-template-areas: "."
    "discription"
    "."
    "img"
    ".";
    > p {
      grid-area: discription;
      font-size: 10px;
    }
    > img {
      position: relative;
      transform:translateX(-50%);
      left: 50%;
      grid-area: img;
      height: 178px;
    }
  }
  > div:nth-child(3){
    grid-area: state-title;
  }
  > div:nth-child(4){
    grid-area: state-content;
    display: grid;
    grid-template-columns: 83px 83px 83px;
    grid-column-gap: 10px;
    .waiting {
      width: 83px;
      height: 83px;
      border: solid 1px map-get($colors, white );
      border-radius: 4px;
      display: grid;
      justify-content: center;
      align-items: center;
      grid-template-rows: 12px 47px 6px 9px 1fr;
      grid-template-areas: "."
      "img"
      "."
      "p"
      ".";
      > img{
        grid-area: img;
        height: 47px;
      }
      > p {
        color: map-get($colors, white );
        grid-area: p;
        font-size: 9px;
      }
    }
    .cool-down {
      border: solid 1px  #FF417C;
      > p {
        color: #FF417C;
      }
    }

  }
  > div:nth-child(5){
    grid-area: button;
    > button {
      padding: 5px;
      width: 97px;
      font-size: 12px;
      border: none;
      border-radius: 21px;
      background: map-get($linear-gradient, primary);
      color: map-get($colors, white);
    }
  }
  > .leave-btn {
    position: absolute;
    top: 17px;
    left: 18px;
    background-color: rgba(170, 105, 181, 0);
    color: white;
    border: none;
    width: 11px;
    height: 11px;
    background-size: cover;
    background-position: center;
    background-image: url("~@/assets/icon/leave-cross.png");
  }
}
</style>
