<template lang="pug">
  div(class="building-info")
    template(v-if="building.status === 'done'")
      div
        h4 {{building.name_zh}}
        button(@click="removeBuilding") 拆除
      div
        button(@click="openModal('建築升級')" class="icon-button")
        button(@click="openModal('建築功能')" class="icon-button")
    template(v-else-if="building.status === 'ing'")
      div
        h4 {{building.name_zh}}
        button(@click="isbuildImmediately = true") 立即完成
      div
        span 剩餘建造時間 {{remainingTime}}
    div(v-if="isbuildImmediately")
      i(:style="{'background-image':`url(${require('@/assets/icon/diamond.png')})`}")
      div
        p 請問是否花費 12寶石， 立即完成建造？
        div
          button(@click="isbuildImmediately = false") 取消
          button(@click="buildImmediately") 確認
</template>

<script>
export default {
  props: {
    building: Object
  },
  data () {
    return {
      module: null,
      remainingTime: '',
      isbuildImmediately: false,
      timer: null
    }
  },
  beforeCreate () {
    import('@/game/scenes/PlayScene').then(async (module) => {
      this.module = module
    })
  },
  beforeMount () {
    clearInterval(this.timer)
    if (this.building.status === 'ing') {
      this.countdown()
      this.timer = setInterval(this.countdown, 1000)
    }
  },
  methods: {
    openModal (one) {
      this.$emit('openModal', one)
    },
    close () {
      this.$emit('close')
    },
    async buildImmediately () {
      try {
        this.isbuildImmediately = false
        await this.$store.dispatch('user/spendDiamond', { diamond: 12 })
        this.module.gameFunction.directlyBuild(this.building)
        this.close()
      } catch (error) {
        console.log(error)
      }
    },
    async removeBuilding () {
      await this.$store.dispatch('planet/removeBuilding', { token: this.$store.getters['user/getToken'], building: this.building })
      await this.module.gameFunction.removeBuilding(this.building)
      this.close()
    },
    countdown: function () {
      if (this.building.status === 'ing') {
        const end = Date.parse(new Date(this.building.finishTime))
        const now = Date.parse(new Date())
        const msec = end - now
        let day = parseInt(msec / 1000 / 60 / 60 / 24)
        let hr = String(parseInt(msec / 1000 / 60 / 60 % 24)).padStart(2, '0')
        let min = String(parseInt(msec / 1000 / 60 % 60)).padStart(2, '0')
        let sec = String(parseInt(msec / 1000 % 60)).padStart(2, '0')
        this.remainingTime = day > 0 ? `${day}天` : ''
        this.remainingTime += `${hr}:${min}:${sec}`
        if (msec <= 0) {
          clearInterval(this.timer)
          this.close()
        }
      }
    }
  },
  watch: {
    isbuildImmediately: function () {
      this.module.gameFunction.scene.input.enabled = !this.isbuildImmediately
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.building-info {
  box-sizing: border-box;
  position: fixed;
  bottom: 0px;
  width: 100%;
  height: 83px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 22px;
  background: linear-gradient(
    0deg,
    rgba(27, 18, 41, 1) 0%,
    rgba(24, 16, 36, 0.89) 25%,
    rgba(15, 10, 23, 0.58) 50%,
    rgba(1, 1, 2, 0.06) 75%
  );
  > div:nth-child(1) {
    display: flex;
    align-items: center;
    > h4 {
      font-size: 24px;
      text-shadow: map-get($shadow, primary-4);
      margin-right: 9px;
    }
    > button {
      padding: 4px 5px;
      border: 1px solid #ff417c;
      border-radius: 2px;
      background-color: map-get($colors, black);
      color: #ff417c;
      box-shadow: map-get($shadow, secondary-4);
      font-size: 9px;
      line-height: 9px;
    }
  }
  > div:nth-child(2) {
    display: flex;
    align-content: center;
    > button {
      width: 29px;
      height: 29px;

      &:nth-child(1) {
        background-image: url("~@/assets/icon/building-upgrade.png");
        margin-right: 11px;
      }
      &:nth-child(2) {
        background-image: url("~@/assets/icon/building-function.png");
      }
    }
  }
  > div:nth-child(3) {
    box-sizing: border-box;
    display: flex;
    width: 283px;
    height: 92px;
    padding: 13px;
    border: 1px solid map-get($colors, white);
    box-shadow: map-get($shadow, secondary-4);
    background-color: rgba($color: map-get($colors, black), $alpha: 0.7);
    border-radius: 7.5px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -40%);

    > i {
      background-size: cover;
      width: 130px;
      height: 130px;
      position: relative;
      top: -50px;
      left: -15px;
    }
    > div {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      > p {
        width: 150px;
        height: 34px;
        font-size: 14px;
        font-weight: 900;
        text-align: left;
      }
      > div {
        display: flex;
        justify-content: flex-end;
        > button {
          font-size: 14px;
          font-weight: 900;
        }
        > button:nth-child(2) {
          color: #ff417c;
          margin-left: 11px;
        }
      }
    }
  }
}
</style>
