<template lang="pug">
  div(class="architecture-technology-deatail-root")
    div
      div(class="image-container")
        div(:class="{unlocked: !archiTech.isLocked}")
          img(:src="showIamge(archiTech.isLocked)")
      div
        h4 {{archiTech.title}}
        span 研發所需點數 : {{archiTech.point}}
        span 狀態：{{archiTech.isLocked ? '未解鎖' : '已解鎖'}}
    div
      div(class="heading-default")
        h4 研發影響
      div(class="content")
        p {{archiTech.description}}
        div
          img(:src="transformImage(archiTech.name)")
    div
      span(@click="back") 返回上頁
      span(v-if="archiTech.isLocked" @click="devArchiTech(archiTech.id)") 進行研發
</template>

<script>
import { transformImage } from '@/assets/javascript/transformImage.js'
import { mapGetters, mapActions } from 'vuex'

export default {
  props: {
    archiTech: Object
  },
  data () {
    return {
      transformImage: transformImage
    }
  },
  computed: {
    ...mapGetters('user', {
      token: 'getToken'
    })
  },
  methods: {
    ...mapActions('planet', ['devArchitectureTechnology']),
    showIamge (isLocked) {
      if (isLocked) {
        return require('@/assets/image/architectureTechnology/book-inactive.png')
      } else {
        return require('@/assets/image/architectureTechnology/book-active.png')
      }
    },
    back () {
      this.$emit('back')
    },
    async devArchiTech (id) {
      this.$emit('nextScenes', '進行研發')
      await this.devArchitectureTechnology({ token: this.token, id })
      this.back()
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.architecture-technology-deatail-root {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 95px 15px 254px 26px 16px;
  grid-template-areas:
    "title"
    "."
    "main"
    "."
    "button-group";
  > div:nth-child(1) {
    grid-area: title;
    margin: 0 auto;
    width: 232px;
    display: grid;
    grid-template-columns: 95px 1fr;
    grid-template-rows: auto;
    grid-column-gap: 25px;
    align-items: center;
    > .image-container {
      box-sizing: border-box;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 95px;
      height: 95px;
      background-color: #242236;
      border-radius: 50%;
      > div {
        width: 85px;
        height: 85px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          width: 45px;
          height: 36px;
        }
      }
      .unlocked {
        border: 1px solid map-get($colors, secondary);
      }
    }
    > div:nth-child(2) {
      display: grid;
      grid-template-rows: auto auto auto;
      grid-row-gap: 12px;
      justify-items: start;
      h4 {
        font-size: 16px;
        line-height: 16px;
        text-shadow: map-get($shadow, secondary-4);
      }
      span {
        font-size: 12px;
        line-height: 12px;
      }
    }
  }
  > div:nth-child(2) {
    grid-area: main;
    .content {
      > p {
        height: 71px;
        padding: 14px 0 18px 0;
        font-size: 11px;
        line-height: 18px;
      }
      > div {
        height: 128px;
        > img {
          height: 128px;
        }
      }
    }
  }
  > div:nth-child(3) {
    grid-area: button-group;
    display: flex;
    justify-content: center;
    > span {
      font-size: 16px;
      line-height: 16px;
      font-weight: 900;
    }
    > span:nth-child(2) {
      color: #ff417c;
      padding-left: 24px;
    }
  }
}
</style>
