<template lang="pug">
  div(class="modal")
    div(class="card")
      slot(name="card")
    div(class="leave" @click="cancelJump")
      button(@click="$emit('close')" class="icon-button")
      span 離開
</template>

<script>
export default {
  data () {
    return {
      module: null
    }
  },
  created: function () {
    import('@/game/scenes/PlayScene').then(async (module) => {
      this.module = module
    })
  },
  methods: {
    cancelJump () {
      this.module.gameFunction.cancelJump()
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.modal {
  height: 100%;
  width: 100%;
  background-color: rgba($color: map-get($colors, black), $alpha: 0.9);
  position: absolute;
  z-index: 100;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  .card {
    position: relative;
    box-sizing: border-box;
    margin: 40px 26px 110px 26px;
    padding: 17px 21px;
    border: 1px solid map-get($colors, white);
    border-radius: 7.5px;
    height: 518px;
    box-shadow: map-get($shadow, secondary-4);
    > div {
      height: 100%;
      overflow-y: scroll;
    }
  }
  .leave {
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
    > button {
      display: block;
      width: 37px;
      height: 37px;
      background-image: url('~@/assets/icon/leave-cross.png');
      padding: 0px;
    }
    > span {
      font-size: 10px;
    }
  }
}
</style>
