function transformImage (type) {
  switch (type) {
    case '赫克斯':
      return require('@/assets/image/planet/planet-1.png')
    case '奎奈爾':
      return require('@/assets/image/planet/planet-2.png')
    case '金幣':
      return require('@/assets/icon/coin.png')
    case '鑽石':
      return require('@/assets/icon/diamond.png')
    case '石頭':
      return require('@/assets/icon/stone.png')
    case '木頭':
      return require('@/assets/icon/wood.png')
    case '鋼鐵':
      return require('@/assets/icon/steel.png')
    case '銅礦':
      return require('@/assets/icon/copper.png')
    case '墨金屬':
      return require('@/assets/icon/ore-1.png')
    case '氪金屬':
      return require('@/assets/icon/ore-2.png')
    case '銝金屬':
      return require('@/assets/icon/ore-3.png')
    case '鍨金屬':
      return require('@/assets/icon/ore-4.png')
    case '巨石陣傳送門':
      return require('@/assets/image/building/stone-portal.png')
    case '風車':
      return require('@/assets/image/building/windmill.png')
    case '太空溫室農場':
      return require('@/assets/image/building/green-house.png')
    case '發電廠':
      return require('@/assets/image/building/power-plant.png')
    case '資源傳輸中心':
      return require('@/assets/image/building/resource-transfer.png')
    case '倉庫':
      return require('@/assets/image/building/warehouse.png')
    case '薛丁格的貓一般':
      return require('@/assets/cat-normal.png')
    case '薛丁格的貓稀有':
      return require('@/assets/cat-rare.png')
    case '薛丁格的貓史詩':
      return require('@/assets/cat-epic.png')
    default:
      break
  }
}

function transformUnlockImage (type) {
  switch (type) {
    case '巨石陣傳送門':
      return require('@/assets/image/building/stone-portal-locked.png')
    case '風車':
      return require('@/assets/image/building/windmill-locked.png')
    case '太空溫室農場':
      return require('@/assets/image/building/green-house-locked.png')
    case '發電廠':
      return require('@/assets/image/building/power-plant-locked.png')
    case '資源傳輸中心':
      return require('@/assets/image/building/resource-transfer-locked.png')
    default:
      break
  }
}
export { transformImage, transformUnlockImage }
