<template lang="pug">
  tab(
    class="architecture-technology-center-root"
    :navList="navList"
    @switchMenu="switchMenu"
    :class=" !isSelect ? 'row-gap-45' : 'row-gap-31'")
      div(slot="content")
        skills-tree(
          v-if="!isSelect"
          @click-node="clickNode"
          :skillsInfo="buildings[currentMenu]"
          :planet="planet")
        architecture-technology-detail(
          v-if="isSelect"
          :archiTech="selectedNode"
          @back="back"
          @nextScenes="nextScenes")
        span(class="point") 科研點數 : {{planet.architectureTechnologyPoint}}
</template>

<script>
import Tab from '@/components/Tab.vue'
import SkillsTree from '@/components/game/build/buildingFeature/SkillsTree.vue'
import archiTechTreeData from '@/assets/data/architecture-technology-tree.json'
import ArchitectureTechnologyDetail from '@/components/game/build/buildingFeature/ArchitectureTechnologyDetail.vue'

export default {
  components: {
    SkillsTree,
    Tab,
    ArchitectureTechnologyDetail
  },
  props: {
    planet: Object
  },
  data () {
    return {
      navList: ['通用科研', '軍事科研', '經濟科研', '生存科研'],
      currentMenu: '通用科研',
      buildings: {
        '通用科研': archiTechTreeData.commonBuilding,
        '軍事科研': archiTechTreeData.militaryBuilding,
        '經濟科研': archiTechTreeData.economicBuilding,
        '生存科研': archiTechTreeData.survivalBuilding
      },
      isSelect: false,
      selectedNode: ''
    }
  },
  methods: {
    switchMenu (one) {
      this.isSelect = false
      this.currentMenu = one
      this.$forceUpdate()
    },
    clickNode: function (node) {
      this.isSelect = true
      this.selectedNode = node
      this.nextScenes(node.name)
    },
    back () {
      this.isSelect = false
    },
    nextScenes (action) {
      this.$emit('nextScenes', action)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.architecture-technology-center-root {
  position: relative;
  /deep/ .nav {
    span {
      font-size: 11px;
      line-height: 11px;
    }
  }
  .point {
    position: absolute;
    top: 39px;
    right: 0px;
    font-size: 10px;
    line-height: 10px;
  }
}
.row-gap-45 {
  grid-row-gap: 45px;
}
.row-gap-31 {
  grid-row-gap: 31px;
}
</style>
