<template lang="pug">
  div(id="list-content")
    div(v-for="planet in planetList")
      div(:style="{'background-image': `url(${transformImage(planet.type)})`}")
      div
        div
          h4 {{planet.name}}
          label(class="army") 軍事
        div
          i
          span ({{planet.positionX}}, {{planet.positionY}})
        div
          h6 等級 Lv.{{planet.level}}
          h6 {{planet.experience}} / {{planet.nextLevel}}
        div
          div(v-bind:style="{width: level(planet.experience, planet.nextLevel) + '%'}")
      div
        div(v-if="planet.pid === $store.getters['planet/getPlanetId']" class="use")
          span 目前
        div(v-else @click="switchPlanet(planet.name)")
          span 前往
</template>

<script>
import { PlanetGameFunc } from '@/game/scenes/PlanetScene'
import { mapState, mapActions } from 'vuex'
import { transformImage } from '@/assets/javascript/transformImage'

export default {
  data: function () {
    return {
      transformImage: transformImage
    }
  },
  async beforeMount () {
    await this.getPlanetList()
  },
  computed: {
    ...mapState('user', {
      planetList: state => state.planetList
    })
  },
  methods: {
    ...mapActions('user', ['getPlanetList']),
    level: function (experience, nextLevel) {
      return 100 * (experience / nextLevel)
    },
    switchPlanet (name) {
      PlanetGameFunc.SwitchPlanet('test')

      // this.planets.map(planet => {
      //   if (planet.name === name) {
      //     planet.state = true
      //   } else {
      //     planet.state = false
      //   }
      // })
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

#list-content {
  display: grid;
  grid-template-columns: 267px;
  grid-auto-rows: 90px;
  grid-row-gap: 14px;
  justify-content: center;
  > div {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 5px;
    border: 1px solid map-get($colors, primary);
    box-shadow: 0 0 2px map-get($colors, primary);
    padding: 7px 9px;
    > div:nth-child(1) {
      width: 76px;
      height: 76px;
      background-size: contain;
      background-position: center;
    }
    > div:nth-child(2) {
      height: 100%;
      width: 107px;
      display: grid;
      grid-template-rows: 10px 12px 12px 10px 8px 9px 5px 4px 6px;
      grid-template-areas:
        "."
        "name"
        "."
        "position"
        "."
        "level"
        "."
        "experience"
        ".";
      > div:nth-child(1) {
        grid-area: name;
        display: flex;
        align-items: center;
        justify-content: space-between;
        > h4 {
          font-size: 12px;
          line-height: 12px;
        }
        > label {
          border-radius: 1.5px;
          font-size: 6px;
          line-height: 12px;
          height: 11px;
          width: 22px;
        }
        .army {
          background-color: #f7797d;
        }
        .survive {
          background-color: #fbb03b;
        }
      }
      > div:nth-child(2) {
        grid-area: position;
        display: flex;
        > i {
          width: 6px;
          height: 9px;
          background-image: url("~@/assets/icon/planet-locate.png");
          margin-right: 5px;
        }
        > span {
          font-size: 9px;
          line-height: 9px;
        }
      }
      > div:nth-child(3) {
        grid-area: level;
        display: flex;
        justify-content: space-between;
        > h6 {
          font-size: 9px;
          line-height: 9px;
        }
      }
      > div:nth-child(4) {
        grid-area: experience;
        background-color: map-get($colors, white);
        height: 4px;
        > div {
          height: 4px;
          background: map-get($linear-gradient, primary);
        }
      }
    }

    > div:nth-child(3) {
      > div {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 3px;
        height: 18px;
        width: 43px;
        border: 1px solid map-get($colors, primary);
      }
      span {
        font-size: 10px;
        line-height: 16px;
      }
      .use {
        background: map-get($linear-gradient, primary);
        border: none;
      }
    }
  }
}
</style>
