<template lang="pug">
  tab(
    class="building-root"
    :navList="navList"
    @switchMenu="switchMenu")
    div(slot="content")
      div(class="selected-building")
        h3 {{selectedBuilding.name_zh}}
        div
          div
            img(:src="transformImage(selectedBuilding.name_zh)")
          div
            div
              p {{selectedBuilding.description}}
            div
              div
                span 建造時間
                span {{selectedBuilding.buildTime}}
              div
                span 花費金幣
                span {{selectedBuilding.cost}}
        div(class="resource-group")
          div(v-for="resource in selectedBuilding.resources" :class="{ notEnough : notEnough(resource)} ")
            i(:style="{'background-image': `url(${transformImage(resource.type)})`}")
            span {{resource.amount}}
        button(@click="onBuildMode(selectedBuilding)" class="button-default" :style="isEnough ? 'opacity: 1' : 'opacity: 0.4'") 建造該建築
      div
        div(v-for="building in buildings[currentOpen]")
          img(
            :src="transformImage(building.name_zh)"
            @click="chooseBuilding(building)"
            :style="selectedBuilding.name_zh === building.name_zh ? 'opacity: 1' : 'opacity: 0.5'")
</template>

<script>
import buildingInfo from '@/assets/data/building-info.json'
import Tab from '@/components/Tab.vue'
import { transformImage } from '@/assets/javascript/transformImage.js'

export default {
  components: {
    Tab
  },
  props: {
    user: Object
  },
  data () {
    return {
      transformImage: transformImage,
      navList: ['通用科研', '軍事科研', '經濟科研', '生存科研'],
      currentOpen: '通用科研',
      selectedBuilding: {},
      buildings: {
        '通用科研': buildingInfo.commonBuilding,
        '軍事科研': buildingInfo.militaryBuilding,
        '經濟科研': buildingInfo.economicBuilding,
        '生存科研': buildingInfo.survivalBuilding
      },
      isEnough: true
    }
  },
  created () {
    this.selectedBuilding = this.buildings['通用科研'][0]
  },
  methods: {
    onBuildMode (selectedBuilding) {
      for (let resource of selectedBuilding.resources) {
        if (this.notEnough(resource)) { return false }
      }
      this.$emit('close')
      this.$emit('onBuildMode', selectedBuilding)
    },
    switchMenu (name) {
      this.currentOpen = name
      this.selectedBuilding = this.buildings[this.currentOpen][0]
    },
    chooseBuilding (building) {
      this.selectedBuilding = building
      this.isEnough = true
    },
    notEnough (resource) {
      const userResource = this.user.resources.find(item => item.type === resource.type)
      if (userResource.amount < resource.amount) {
        this.isEnough = false
        return true
      }
      return false
    }
  }

}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.building-root {
  /deep/ .nav {
    span {
      font-size: 11px;
      line-height: 11px;
    }
  }
  > div:nth-child(2) {
    display: grid;
    grid-template-rows: 344px 93px;
    > .selected-building {
      box-sizing: border-box;
      border-bottom: 1px solid map-get($colors, primary);
      display: grid;
      grid-template-rows: 20px 27px 145px 34px 66px 14px 25px 13px;
      grid-template-areas:
        "title"
        "."
        "building"
        "."
        "resource"
        "."
        "build"
        ".";
      > h3:nth-child(1) {
        grid-area: title;
        font-size: 20px;
        line-height: 20px;
        text-shadow: map-get($shadow, secondary-8);
      }
      > div:nth-child(2) {
        grid-area: building;
        display: grid;
        grid-template-columns: 148px 120px;
        grid-template-rows: 145px;
        grid-column-gap: 11px;
        > div:nth-child(1) {
          display: flex;
          justify-content: center;
          align-items: center;
          > img {
            width: 100%;
          }
        }
        > div:nth-child(2) {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          > div:nth-child(1) {
            > p {
              text-align: justify;
              font-size: 10px;
            }
          }
          > div:nth-child(2) {
            > div {
              display: flex;
              justify-content: space-between;
              padding: 8px 8px 0;
              font-size: 10px;
              border-bottom: 1px solid map-get($colors, primary);
            }
          }
        }
      }
      > div:nth-child(3) {
        grid-area: resource;
      }
      > button:nth-child(4) {
        grid-area: build;
        justify-self: center;
        width: 97px;
        height: 25px;
        font-size: 12px;
      }
    }
    > div:nth-child(2) {
      overflow-x: scroll;
      overflow-y: hidden;
      display: grid;
      grid-auto-columns: 70px;
      grid-template-rows: 93px;
      grid-auto-flow: column;
      grid-column-gap: 10px;
      align-items: flex-end;
      > div {
        > img {
          width: 100%;
        }
      }
      &::-webkit-scrollbar {
        display: none;
        // width: 0 !important;
        -ms-overflow-style: none;
        overflow: -moz-scrollbars-none;
      }
    }
  }
}
.notEnough {
  border: 1px solid #ff417c;
  > span {
    color: #ff417c;
  }
}
</style>
