<template lang="pug">
  div(class="dialog-root" :style="dialogData[currentScenes].position" @click="$emit('nextScenes', 'dialog')")
    div(
    class="dialog-wrapper"
    :style="dialogData[currentScenes].style")
      span 拉維多長老
      template(v-for="message in dialogData[currentScenes].messageArray")
        p {{message}}
      i(:style="{'background-image': `url(${require('@/assets/image/beginnerGuide/touch-dot.png')})`}")
      div
        img(:src="require('@/assets/image/beginnerGuide/old-man.png')")
</template>

<script>
export default {
  props: {
    currentScenes: Number
  },
  data () {
    return {
      dialogData: [
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '82px'
          },
          messageArray: [
            '歡迎來到XXX星球，',
            '我接下來將一步一步帶領你',
            '建造屬於自己的星球文明。'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          messageArray: [
            '請先點擊下方建造按鈕，',
            '進行建築的興建。'
          ]
        },
        {
          position: {
            bottom: '34px'
          },
          style: {
            height: '82px'
          },
          messageArray: [
            '這裡是建造頁面，',
            '顯示可建造建築的資訊，',
            '以及需要花費的時間與資源。'
          ]
        },
        {
          position: {
            bottom: '34px'
          },
          style: {
            height: '60px'
          },
          messageArray: [
            '請先點擊「建造該建築」按鈕，',
            '讓我們來建造「科研中心」吧！'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '82px'
          },
          messageArray: [
            '點擊地圖上其中一個位置，',
            '並點擊下方的「建造」按鈕，',
            '即可進行建造。'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          messageArray: [
            '等待建造時間結束後，',
            '再點擊「建造完成」即可完成。'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          messageArray: [
            '現在你已經擁有「科研中心」，',
            '接著請點擊圖面上「科研中心」建築。'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '82px'
          },
          messageArray: [
            '下方左側顯示該建築的名稱，',
            '右側具有「升級」與「建築功能」按鈕，',
            '請先點擊星號「建築功能」按鈕。'
          ]
        },
        {
          position: {
            bottom: '34px'
          },
          style: {
            height: '107px'
          },
          messageArray: [
            '每個建築的功能不一樣，',
            '由「科研中心」為例，',
            '其功能為研發新科技，',
            '並且解鎖各種建築物的建造能力。'
          ]
        },
        {
          position: {
            bottom: '34px'
          },
          style: {
            height: '82px'
          },
          messageArray: [
            '科技研發必須從上而下，',
            '具備必備科技後才能發展更高階的技術，',
            '讓我們先點擊上面第二個圖示吧！'
          ]
        },
        {
          position: {
            bottom: '34px'
          },
          style: {
            height: '82px'
          },
          messageArray: [
            '點擊「進行研發」，',
            '將可以對「安提基特拉機械」做研究，',
            '並且獲得建造「資源傳輸中心」的能力。'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          messageArray: [
            '點擊「離開」，',
            '返回遊戲主畫面。'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          messageArray: [
            '現在請再次點擊「建造」按鈕，',
            '讓我們來興建「資源採集中心」。'
          ]
        },
        {
          position: {
            bottom: '34px'
          },
          style: {
            height: '82px'
          },
          messageArray: [
            '點擊下方的縮圖即可切換建築，',
            '找到「資源採集中心」，',
            '並且按下「建造該建築」吧！'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '82px'
          },
          messageArray: [
            '點擊地圖上其中還沒有建築的位置，',
            '並點擊下方的「建造」按鈕，',
            '即可進行建造。'
          ]
        },
        {}, {}, {}, {}, // 動畫場景
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '82px'
          },
          messageArray: [
            '由於外太空缺乏一些地球才有的資源，',
            '我們需要嘗試回到地球探勘並且採集，',
            '請點擊下方的「採集」按鈕。'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '82px'
          },
          messageArray: [
            '目前在地球上的波波拉，',
            '狀態趨緩不再失控，',
            '我們可以在您所在的格子上進行資源採集。'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '101px'
          },
          messageArray: [
            '左上方為待命中的採集機器人數量，',
            '與玩家資源存量。',
            '當仍有剩餘的採集機器人時，',
            '即可進行採集功能。'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          messageArray: [
            '現在請先點擊定位點所在的格子，',
            '下方將會顯示該格的地表資訊。'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          messageArray: [
            '接下來按下「進行採集」按鈕，',
            '將可進行採集。'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          messageArray: [
            '這樣就採集成功了！',
            '現在請再點選一次定位點所在的格子。'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '115px'
          },
          messageArray: [
            '採集成功後，你會得到基本資源，',
            '並且該格會進入冷卻時間、',
            '採集機器人也會減少一個，',
            '該格在冷卻結束前，',
            '無法再對此格進行採集。'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '82px'
          },
          messageArray: [
            '不過若有剩餘的採集機器人，',
            '仍可走到其他格子，',
            '對其他格子進行採集。'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '101px'
          },
          messageArray: [
            '此外，若該格被採集者們採集殆盡，',
            '該格的波波拉將會失控，',
            '無法再度採集資源，',
            '須等待資源重生後，才可以進行採集。'
          ]
        },
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          messageArray: [
            '新手引導到此結束，',
            '請按右上角X，返回星球'
          ]
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.dialog-root {
  position: fixed;
  z-index: 300;
  left: 50%;
  transform: translateX(-50%);
  .dialog-wrapper {
    box-sizing: border-box;
    width: 339px;
    padding: 15px 16px;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 3px 15px #ffffff;
    text-align: left;
    > span {
      font-size: 14px;
      line-height: 14px;
      font-weight: 700;
      position: absolute;
      top: -18px;
      left: 16px;
    }
    p {
      color: map-get($colors, black);
      font-size: 12px;
      line-height: 18px;
    }
    i {
      position: absolute;
      bottom: 16px;
      right: 10px;
      width: 10px;
      height: 10px;
    }
    > div {
      position: absolute;
      width: 85px;
      height: 226px;
      right: 15px;
      bottom: -24px;
      > img {
        width: 100%;
      }
    }
  }
}
</style>
